.menu-button {
  background-color: inherit;
  border: none;
  cursor: pointer;

  //  ,  "" - 
  &--close {
    background-image: url("../img/stack.svg#menu-mobile-close");
    background-size: 20px 20px;
    background-repeat: no-repeat;
    background-position: center;
    width: 30px;
    height: 30px;
    padding: 5px;
    position: absolute;
    top: -51px;
    right: 18px;
  }

  //  ,  "" - 
  &--open {
    background-image: url("../img/stack.svg#menu-mobile-open");
    background-size: 24px 18px;
    background-repeat: no-repeat;
    background-position: center;
    width: 30px;
    height: 30px;
    padding: 3px;
    position: absolute;
    top: -50px;
    right: 18px;
  }
}
